'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Habit } from '@/lib/api';

interface HabitFormProps {
    habit?: Habit;
    onSubmit: (habit: Omit<Habit, 'id' | 'createdAt' | 'updatedAt'>) => void;
    onCancel: () => void;
}

const defaultHabit: Omit<Habit, 'id' | 'createdAt' | 'updatedAt'> = {
    name: '',
    description: '',
    color: '#3B82F6',
    userId: '',
};

export default function HabitForm({ habit = defaultHabit, onSubmit, onCancel }: HabitFormProps) {
    const [formData, setFormData] = useState({
        name: habit.name,
        description: habit.description || '',
        color: habit.color,
        userId: habit.userId || '',
    });

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        onSubmit(formData);
    };

    return (
        <form onSubmit={handleSubmit} className="space-y-4 p-4 border rounded-lg">
            <div className="space-y-2">
                <label htmlFor="name" className="text-sm font-medium">
                    Habit Name
                </label>
                <Input
                    id="name"
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    placeholder="e.g., Drink water"
                    required
                />
            </div>

            <div className="space-y-2">
                <label htmlFor="description" className="text-sm font-medium">
                    Description (Optional)
                </label>
                <Textarea
                    id="description"
                    value={formData.description}
                    onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                    placeholder="e.g., Drink at least 8 glasses of water daily"
                />
            </div>

            <div className="space-y-2">
                <label htmlFor="color" className="text-sm font-medium">
                    Color
                </label>
                <div className="flex items-center space-x-2">
                    <input
                        type="color"
                        id="color"
                        value={formData.color}
                        onChange={(e) => setFormData({ ...formData, color: e.target.value })}
                        className="w-10 h-10 rounded border border-input"
                    />
                    <span className="text-sm text-muted-foreground">{formData.color}</span>
                </div>
            </div>

            <div className="flex justify-end space-x-2 pt-4">
                <Button type="button" variant="outline" onClick={onCancel}>
                    Cancel
                </Button>
                <Button type="submit">{habit.id ? 'Update' : 'Add'} Habit</Button>
            </div>
        </form>
    );
}