'use client';

import { useState } from 'react';
import { Check, MoreHorizontal, Trash2, Pencil } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from '@/components/ui/dropdown-menu';
import { Habit } from '@/lib/api';
import HabitForm from './HabitForm';
import { useHabits } from '@/contexts/HabitContext';

interface HabitItemProps {
    habit: Habit;
}

export default function HabitItem({ habit }: HabitItemProps) {
    const [isEditing, setIsEditing] = useState(false);
    const { updateHabit, removeHabit, toggleCompletion } = useHabits();

    if (isEditing) {
        return (
            <HabitForm
                habit={habit}
                onCancel={() => setIsEditing(false)}
                onSubmit={async (updatedHabit) => {
                    await updateHabit({ ...updatedHabit, id: habit.id });
                    setIsEditing(false);
                }}
            />
        );
    }

    return (
        <div className="flex items-center justify-between p-4 border rounded-lg">
            <div className="flex items-center space-x-3">
                <button
                    onClick={() => toggleCompletion({
                        habitId: habit.id,
                        userId: habit.userId,
                        completionDate: new Date().toISOString().split('T')[0],
                        completed: true,
                    })}
                    className={`w-6 h-6 rounded-full border-2 flex items-center justify-center ${
                        true ? 'bg-primary border-primary text-primary-foreground' : 'border-muted-foreground'
                    }`}
                >
                    {true && <Check className="h-4 w-4" />}
                </button>
                <div>
                    <h3 className="font-medium">{habit.name}</h3>
                    {habit.description && <p className="text-sm text-muted-foreground">{habit.description}</p>}
                </div>
            </div>
            <DropdownMenu>
                <DropdownMenuTrigger asChild>
                    <Button variant="ghost" size="icon">
                        <MoreHorizontal className="h-4 w-4" />
                        <span className="sr-only">More</span>
                    </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                    <DropdownMenuItem onClick={() => setIsEditing(true)}>
                        <Pencil className="mr-2 h-4 w-4" />
                        <span>Edit</span>
                    </DropdownMenuItem>
                    <DropdownMenuItem
                        className="text-destructive"
                        onClick={async () => {
                            if (confirm('Are you sure you want to delete this habit?')) {
                                await removeHabit(habit.id);
                            }
                        }}
                    >
                        <Trash2 className="mr-2 h-4 w-4" />
                        <span>Delete</span>
                    </DropdownMenuItem>
                </DropdownMenuContent>
            </DropdownMenu>
        </div>
    );
}